	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"The 10 commandments of system administration","date":"27 Jun 2005","slug":"/2005-06-27-the-10-commandments-of-system-administration/","tags":"Y2005"},"html":"<p>The 10 commandments of system administration, from <a href=\"http://www.newsforge.com/\">NewsForge</a>:<br />\nI. <a href=\"//software.newsforge.com/article.pl?sid=05/04/15/2130246&tid=91\">Thou shalt make regular and complete backups</a><br />\nII. <a href=\"//software.newsforge.com/article.pl?sid=05/04/25/1926246&tid=152\">Thou shalt establish absolute trust in thy servers</a><br />\nIII. <a href=\"//software.newsforge.com/article.pl?sid=05/05/02/1426209&tid=91\">Thou shalt be the first to know when something goes down</a><br />\nIV. <a href=\"//software.newsforge.com/article.pl?sid=05/05/09/198203&tid=91\">Thou shalt keep server logs on everything</a><br />\nV. <a href=\"//software.newsforge.com/article.pl?sid=05/05/13/1851255&tid=152\">Thou shalt document complete and effective policies and procedures</a><br />\nVI. <a href=\"//software.newsforge.com/article.pl?sid=05/05/20/1446206&tid=91\">Thou shalt know what cable goes where</a><br />\nVII. <a href=\"//software.newsforge.com/article.pl?sid=05/06/01/1631201&tid=91\">Thou shalt use encryption for insecure services</a><br />\nVIII. <a href=\"//software.newsforge.com/article.pl?sid=05/06/03/2032258&tid=91\">Thou shalt not lose system logs when a server dies</a><br />\nIX. <a href=\"//software.newsforge.com/article.pl?sid=05/06/13/2034239&tid=91\">Thou shalt know the openings into your servers</a><br />\nX. <a href=\"http://software.newsforge.com/software/05/06/20/1438209.shtml?tid=91\">Thou shalt not waste time doing repetitive and mundane tasks</a></p>\n<p>We're still working on full compliance.</p>"},"next":{"fields":{"title":"Tas Kaf","date":"27 Jun 2005","slug":"/2005-06-27-tas-kaf/"}},"previous":{"fields":{"title":"Chaos Management","date":"30 Jun 2005","slug":"/2005-06-30-chaos-management/"}}},{"node":{"fields":{"title":"Tas Kaf","date":"27 Jun 2005","slug":"/2005-06-27-tas-kaf/","tags":"Y2005"},"html":"<p><a href=\"http://homepage.univie.ac.at/horst.prillinger/blog/archives/2005/06/001199.html\" title=\"The Aardvark Speaks: No coffee\">German waiters in Vienna</a>, and they can't serve decent coffee, at least not to Horst.</p>"},"next":{"fields":{"title":"Mickey D's and Me","date":"27 Jun 2005","slug":"/2005-06-27-mickey-ds-and-me/"}},"previous":{"fields":{"title":"The 10 commandments of system administration","date":"27 Jun 2005","slug":"/2005-06-27-the-10-commandments-of-system-administration/"}}},{"node":{"fields":{"title":"Mickey D's and Me","date":"27 Jun 2005","slug":"/2005-06-27-mickey-ds-and-me/","tags":"Y2005"},"html":"<p><a href=\"http://www.nationalreview.com/interrogatory/whaley_200506230747.asp\" title=\"Interview with Soso Whaley, director of Mickey D's and Me, counterpoint to Morgan Spurlock's Super Size Me\">Interview with Soso Whaley</a>, director of Mickey D's and Me, counterpoint to Morgan Spurlock's Super Size Me. She <a href=\"http://www.techcentralstation.com/090804G.html\">lost weight and improved her cholesterol</a> after 30 days of McDonald's. I imagine the <a href=\"http://www.nationalreview.com/\">National Review</a> and the <a href=\"http://cei.org/\">Competitive Enterprise Institute</a> always stick up for poor defenseless corporations, but the point remains that you can eat stupid anywhere, not just at McDonald's. <a href=\"http://www.kottke.org/remainder/05/06/8729.html\">(via kottke)</a></p>"},"next":{"fields":{"title":"Red Jack","date":"25 Jun 2005","slug":"/2005-06-25-red-jack/"}},"previous":{"fields":{"title":"Tas Kaf","date":"27 Jun 2005","slug":"/2005-06-27-tas-kaf/"}}}],"pathPrefix":"","first":false,"last":false,"index":300,"pageCount":1101}}

